.root{height:100vh;width:100vw;display:flex;justify-content:center;align-items:center;color:#fff}


.hero{display:flex;flex-direction:row;justify-content:center;align-items:center;border-radius:3px;font-size:3rem;padding:5rem;width:90vw;color:#fff;background-color:rgba(81,108,240,0.5)}@media (max-width: 60em){.hero{padding:2rem 1rem;color:#222}}

@-webkit-keyframes scaleUp{0%{-webkit-transform:scale(0.95);transform:scale(0.95);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes scaleUp{0%{-webkit-transform:scale(0.95);transform:scale(0.95);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}.animation-scale-up{-webkit-animation:scaleUp 0.5s forwards;animation:scaleUp 0.5s forwards}@-webkit-keyframes scaleDown{0%{-webkit-transform:scale(1.2);transform:scale(1.2);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes scaleDown{0%{-webkit-transform:scale(1.2);transform:scale(1.2);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}.animation-scale-down{-webkit-animation:scaleDown 0.5s forwards;animation:scaleDown 0.5s forwards}@-webkit-keyframes slideDown{0%{opacity:0;-webkit-transform:translateY(-5%);transform:translateY(-5%)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes slideDown{0%{opacity:0;-webkit-transform:translateY(-5%);transform:translateY(-5%)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}.animation-slide-down{-webkit-animation:slideDown 0.5s forwards;animation:slideDown 0.5s forwards}@-webkit-keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@keyframes fadeOut{0%{opacity:1}100%{opacity:0}}.animation-fade-out{-webkit-animation:fadeOut 0.5s forwards;animation:fadeOut 0.5s forwards}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.animation-fade-in{-webkit-animation:fadeIn 0.5s forwards;animation:fadeIn 0.5s forwards}@-webkit-keyframes slideUp{0%{opacity:0;-webkit-transform:translateY(5%);transform:translateY(5%)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes slideUp{0%{opacity:0;-webkit-transform:translateY(5%);transform:translateY(5%)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}.animation-slide-up{-webkit-animation:slideUp 0.5s forwards;animation:slideUp 0.5s forwards}@-webkit-keyframes blurDown{0%{-webkit-filter:blur(15px);filter:blur(15px)}100%{-webkit-filter:blur(0);filter:blur(0)}}@keyframes blurDown{0%{-webkit-filter:blur(15px);filter:blur(15px)}100%{-webkit-filter:blur(0);filter:blur(0)}}@-webkit-keyframes blurUpDown{0%{-webkit-filter:blur(0);filter:blur(0)}50%{-webkit-filter:blur(15px);filter:blur(15px)}100%{-webkit-filter:blur(0);filter:blur(0)}}@keyframes blurUpDown{0%{-webkit-filter:blur(0);filter:blur(0)}50%{-webkit-filter:blur(15px);filter:blur(15px)}100%{-webkit-filter:blur(0);filter:blur(0)}}@-webkit-keyframes scaleUpDown{0%{-webkit-transform:scale(0.8);transform:scale(0.8)}50%{-webkit-transform:scale(1.5);transform:scale(1.5)}100%{-webkit-transform:scale(0.8);transform:scale(0.8)}}@keyframes scaleUpDown{0%{-webkit-transform:scale(0.8);transform:scale(0.8)}50%{-webkit-transform:scale(1.5);transform:scale(1.5)}100%{-webkit-transform:scale(0.8);transform:scale(0.8)}}.scale-down-appear{opacity:0;-webkit-transform:scale(1.1);transform:scale(1.1)}.scale-down-appear-active{opacity:1;-webkit-transform:scale(1);transform:scale(1);-webkit-transition:all 600ms ease-out;transition:all 600ms ease-out}.scale-down-enter{opacity:0;-webkit-transform:scale(1.1);transform:scale(1.1)}.scale-down-enter-active{opacity:1;-webkit-transform:scale(1);transform:scale(1);-webkit-transition:all 300ms ease-out 300ms;transition:all 300ms ease-out 300ms}.scale-down-exit{-webkit-transform:scale(1);transform:scale(1);opacity:1}.scale-down-exit-active{opacity:0;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:all 300ms ease-out;transition:all 300ms ease-out}*,*::after,*::before{margin:0;padding:0;box-sizing:inherit;font-family:'Quicksand', sans-serif}html{font-size:62.5% !important;background:#2b2d36}body{font-size:1.4rem}.typo-align-center{text-align:center}

